<template>
  <div @mouseleave="onleave" class="page__portfolio-detail row">
    <innfos-detail v-if="routeName === 'innfos'"></innfos-detail>
    <aside-list firstListTitle="Smart Technology" secondListTitle="Biotechnology" :firstLists="smartTechs" :secondLists="bioTechs"></aside-list>
  </div>
</template>

<script>
import AsideList from '../components/aside-list';
import INNFOSSection from '@/components/innfos-detail';

export default {
  name: 'portfolioDetail',
  data() {
    return {
      smartTechs: [
        {
          name: 'INNFOS',
          url: '/portfolio/innfos'
        }
      ],
      bioTechs: []
    }
  },
  computed: {
    routeName() {
      return this.$route.params.name;
    }
  },
  methods: {
    onleave(e) {
      const menu = document.querySelectorAll('.aside-list__ul')[0];
      menu.className = menu.className.replace(' hover', '');
    },
  },
  components: {
    'aside-list': AsideList,
    'innfos-detail': INNFOSSection
  }
}
</script>

<style lang="scss">
@import '../App.scss';

</style>


