<template>
  <aside class="columns small-3 aside-list__aside">
    <section class="aside-list">
      <a href="#" @mousemove="onhover" @mouseleave="onleave">
        <h4 class="aside-list__header title--underline">
          {{firstListTitle}}
        </h4>
        <ul class="aside-list__ul">
          <li class="aside-list__li" v-for="item in firstLists">
            <router-link :to="item.url" class="aside-list__alink">{{item.name}}</router-link>
          </li>
        </ul>
      </a>
    </section>
    <section class="aside-list">
      <a href="#" @mousemove="onhover" @mouseleave="onleave">
        <h4 class="aside-list__header title--underline">
          {{secondListTitle}}
        </h4>
        <ul class="aside-list__ul">
          <li class="aside-list__li" v-for="item in secondLists">
            <router-link :to="item.url" class="aside-list__alink">{{item.name}}</router-link>
          </li>
        </ul>
      </a>
    </section>
  </aside>
</template>

<script>
export default {
  name: 'asideList',
  props: {
    firstListTitle: {
      type: String
    },
    firstLists: {
      type: Array
    },
    secondListTitle: {
      type: String
    },
    secondLists: {
      type: Array
    }
  },
  methods: {
    onhover(e) {
      e.stopPropagation();
      const menu = document.querySelectorAll('.aside-list__ul')[0];
      const className = menu.className;
      if (e.target.className.indexOf('aside-list__alink') < 0) {
        if (className.indexOf('hover') < 0) {
          menu.className += ' hover';
        } else {
          menu.className = menu.className.replace(' hover', '');
        }
      }
    },
    onleave(e) {
      const menu = document.querySelectorAll('.aside-list__ul')[0];
      menu.className = menu.className.replace(' hover', '');
    },
  }
}
</script>

<style lang="scss">
@import '../App.scss';

.aside-list {
  margin-top: rem-calc(50);
  text-align: left;
  .title--underline {
    border-bottom: 2px solid red;
    color: #252525;
    display: inline-block;
    font-size: 1.655469vw;
    font-weight: normal;
  }
  @media screen and (max-width: 480px) {
    text-align: center;
    margin-top: 0;
    padding-top: rem-calc(10);
    background: rgb(247, 247, 247);
  }
}

.aside-list__li {
  list-style: none;
  .router-link-active {
    color: red;
  }
}
.aside-list__header {
  font-family: font033;
  color: #1b1b1b;
  margin-bottom: rem-calc(15);
  padding-bottom: rem-calc(6);

  @media screen and (max-width: 480px) {
    font-size: rem-calc(18) !important;
    width: 100%;
    margin-bottom: 0;
  }
}

.aside-list__ul {
  margin-left: 0;
  a {
    font-family: font862;
    color: #898989;
    font-size: 18px;
    font-weight: normal;
    line-height: 1.5;
    &:hover,
    &:active,
    &:focus {
      color: red;
    }
  }
  @media screen and (max-width: 480px) {
    display: none;
    &.hover {
      display: block;
    }
  }
}
</style>


