<template>
  <section class="section__card section__team section--1 columns small-9">
    <header class="page__title">
      <h4>Team</h4>
    </header>
    <div class="row profile__list">
      <h3 class="article__title team__category small-12">
        Investment Partners
      </h3>
      <div v-for="nameCard in investmentNameCards" class="profile__card--horizontal small-6">
        <router-link :to="`${nameCard.url}`">
          <img :src="`/static/namecards/${nameCard.imgSrc}.jpg`" class="profile__image" :alt="nameCard.alt">
        </router-link>
      </div>
    </div>
    <div class="row profile__list">
      <h3 class="article__title team__category small-12">
        Operating Partners
      </h3>
      <div v-for="nameCard in operatingNameCards" class="profile__card--vertical small-3">
        <router-link :to="`${nameCard.url}`">
          <img :src="`/static/namecards/${nameCard.imgSrc}.jpg`" class="profile__image" :alt="nameCard.alt">
          <div class="profile__detail">
            <span class="profile__name">{{nameCard.name}}</span>
            <span class="profile__title">{{nameCard.personTitle}}</span>
          </div>
        </router-link>
      </div>
    </div>
  </section>
</template>

<script>

export default {
  name: 'teamSummary',
  props: {
    persons: {
      type: Array
    }
  },
  computed: {
    investmentNameCards() {
      return this.persons.filter((p) => p.type === 0).map((p) => {
        p.imgSrc = p.name.replace(' ', '').toLowerCase() + 'big';
        return p;
      });
    },
    operatingNameCards() {
      return this.persons.filter((p) => p.type === 1).map((p) => {
        if (!p.imgSrc) {
          p.imgSrc = p.name.replace(' ', '').toLowerCase();
        }
        return p;
      });
    }
  }
}
</script>



