<template>
  <section class="section__person columns small-9">
    <header class="page__title">
      <h4>Team</h4>
    </header>
    <article class="section__article">
      <h3 class="team__category article__title">{{person.personTitle}}</h3>
      <div class="person__image">
        <img :src="`/static/namecards/${person.imgSrc}.jpg`" alt="">
      </div>
      <div class="person__detail">
        <h4 class="person__name">{{person.name}}</h4>
        <p v-for="paragraph in person.profile" class="person__profile">
          {{paragraph}}
        </p>
        <div class="article__footer">
          <a class="person__email" :href="`mailto:${person.email}`">{{person.email}}</a>
          <router-link to="/team" class="link__back"><span>Back</span></router-link>
        </div>
      </div>
    </article>
  </section>
</template>
<script>

export default {
  name: 'sectionPerson',
  props: {
    persons: {
      type: Array
    }
  },
  computed: {
    person() {
      const person = this.persons.filter((p) => p.url.indexOf(this.$route.params.name) >= 0)[0];
      if (!person.imgSrc || person.type === 0) {
        person.imgSrc = person.name.replace(' ', '').toLowerCase();
      }
      return person;
    }
  },
}
</script>

<style lang="scss">
@import '../App.scss';

.section__person {
  text-align: left;
  padding-left: rem-calc(50) !important;

  @media screen and (max-width: 480px) {
    padding-top: rem-calc(60);
    max-width: 100%;
    flex: 0 0 100%;
  }
}

.person__name {
  font-family: font141;
  font-size: 35px;
  color: #231815;
  font-style: italic;
}

.person__profile,
.person__email {
  font-family: font858;
  color: #231815;
  font-size: 18px;
}

.person__detail {
  display: block;
  float: right;
  width: 63%;
}

.person__email {
  color: #e60012;
  font-size: 15px;
}

.article__footer {
  margin-top: rem-calc(50);
}

.link__back {
  cursor: pointer;
  border: 0px !important;
  color: #e60012;
  float: right;
}
.person__image {
  width: 30%;
  float: left;
  margin-right: rem-calc(25);
}
</style>


