<template>
  <div class="page__philosophy">
    <carousel @pageChange="onPageChange" :perPage="1" :autoplay="false" :autoplayHoverPause="true" :autoplayLoop="true" class="gallery__slide section__philosophy">
      <slide class="carousel__slide">
        <div class="vertical--center slide__content">
          <h3 class="slide__title">
            Envision
          </h3>
          <p class="slide__subtitle">
            foreseeing <span class="subtitle--highlight">the future, catalysing</span> technology revolutions
          </p>
        </div>
      </slide>
      <slide class="carousel__slide slide__magnet">
        <div class="vertical--center slide__content">
          <h4 class="slide__title">
            Magnet Ventures focus on seed and early stage investments in technology
          </h4>
        </div>
      </slide>
      <slide class="carousel__slide slide__bio">
        <div class="vertical--center slide__content">
          <h3 class="slide__title">
            Biotechnology
          </h3>
          <p class="slide__article">
            We seek out startups that make innovations in the areas of immune engineering, precision medicine solutions, in vitro diagnostic devices, new business model for DNA sequencing and analysis, etc.
          </p>
        </div>
      </slide>
      <slide class="carousel__slide slide__bio">
        <div class="vertical--center slide__content">
          <h3 class="slide__title">
            Smart Technology
          </h3>
          <p class="slide__article">
            At Magnet Ventures, we fundamentally like startups that have already gained advantages in obtaining/creating raw data. We believe all barriers of smart technology will be converged to accessing raw data and specialized hardware optimization.
          </p>
        </div>
      </slide>

    </carousel>
  </div>
</template>
<script>
import { Carousel, Slide } from 'vue-carousel';
import bioBG from '../assets/backgrounds/biobg.jpg';
import innfosBG from '../assets/backgrounds/innfosbg.jpg';
import magnetBG from '../assets/backgrounds/magnetbg.jpg';
export default {
  name: 'PhilosophyPage',
  mounted() {
    this.setSlideSize();
    var timer = 1;
    window.onresize = () => {
      if (timer) {
        clearTimeout(timer);
        timer = setTimeout(() => {
          this.setSlideSize();
        }, 400);
      }
    }
  },
  methods: {
    setSlideSize() {
      const innerHeight = window.innerHeight;
      const innerWidth = window.innerWidth;
      const headerDOM = document.querySelectorAll('.top-bar');
      const headerHeight = headerDOM[0].clientHeight;
      const slideDOM = document.querySelectorAll('.carousel__slide');
      slideDOM.forEach((s) => {
        s.style.height = innerHeight - headerHeight + 10 + 'px';
      });
    },
    onPageChange(pageNumber) {
      const carouselPageDOM = this.$children[0].$el;
      switch(pageNumber) {
        case 0:
        case 1:
          carouselPageDOM.style.background = "#fff";
          break;
        case 2:
          carouselPageDOM.style.background = `url(${bioBG})`;
          break;
        case 3:
          carouselPageDOM.style.background = `url(${innfosBG})`;
          break;
        default:
          break;
      }
    }
  },
  components: {
    carousel: Carousel,
    slide: Slide
  }
}
</script>

<style lang="scss">
@import '../App.scss';
.vertical--center {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  margin-left: rem-calc(30);
  margin-right: rem-calc(30);

  @media screen and (max-width: 480px) {
    margin-left: rem-calc(0);
    margin-right: rem-calc(0);
    padding-left: rem-calc(10);
    padding-right: rem-calc(10);
  }
}

.section__card {
  text-align: left;
}

.carousel__slide {
  display: block;
  width: 100%;
}

.gallery__slide {
  display: block;
  float: left;
  background-size: auto 100%;
}

.page__philosophy {
  .VueCarousel-dot--active {
    .VueCarousel-dot-inner {
      background: red !important;
    }
  }
  .section__philosophy {
    width: 100%;
  }
  .slide__title {
    font-family: font014;
    color: #000;
    font-size: 13.6484375vw;
    line-height: 13.6484375vw;
    margin-bottom: 0;
  }
  .slide__subtitle {
    color: #000;
    font-size: 1.9vw;
    .subtitle--highlight {
      font-family: font862;
    }
  }
  .slide__article {
    font-family: font858;
  }
}

.slide__magnet {
  background-image: url('../assets/backgrounds/magnetbg.jpg');
  background-size: 50%;
  background-position: 57% 50%;
  background-repeat: no-repeat;
  .slide__content {
    margin-left: 15%;
    margin-right: 15%;
  }
  .slide__title {
    font-family: font033;
    font-size: 4vw;
    line-height: 6vw;
  }
}

.slide__bio {
  text-align: left;
  background-repeat: no-repeat;
  .slide__content {
    margin-left: 40%;
  }
  .slide__title {
    color: #fff;
    font-size: 7vw;
  }
  .slide__article {
    color: #fff;
    margin-top: 10px;
    font-size: 1.9vw;
  }
}
</style>


