<template>
  <div @mouseleave="onleave" class="page__team row">
    <router-view :persons="persons"></router-view>
    <aside-list firstListTitle="Investment Partners" secondListTitle="Operating Partners" :firstLists="investmentPartners" :secondLists="operatingPartners"></aside-list>
  </div>
</template>
<script>
import AsideList from '../components/aside-list';
export default {
  name: 'TeamPage',
  data() {
    return {
      persons: [
        {
          name: 'Georgia Lu',
          alt: "Georgia Lu's name card",
          url: '/team/georgia-lu',
          personTitle: 'Investment Partner',
          email: 'Georgia.Lu@magnet.ventures',
          profile: ['Ben is an investment partner at Khosla Ventures, where he focuses on consumer, Internet, mobile, marketplace, SaaS and consumer health businesses. Since joining the firm, he’s led investments in Academia.edu, Canary, HackerRank and Tapingo, among many others.'],
          type: 0
        },
        {
          name: 'Yanan Zhao',
          alt: "Yanan Zhao's name card",
          url: '/team/yanan-zhao',
          personTitle: 'Investment Partner',
          email: '',
          profile: [''],
          type: 0
        },
        {
          name: 'Zhenyu Yin',
          alt: "Zhenyu Yin's name card",
          url: '/team/zhenyu-yin',
          personTitle: 'Technology Consultant',
          type: 1
        },
        {
          name: 'Zhenyu Yin',
          alt: "Zhenyu Yin's name card",
          url: '/team/zhenyu-yin',
          personTitle: 'Technology Consultant',
          type: 1
        },
        {
          name: 'Zhenyu Yin',
          alt: "Zhenyu Yin's name card",
          url: '/team/zhenyu-yin',
          personTitle: 'Technology Consultant',
          type: 1
        },
        {
          name: 'Zhenyu Yin',
          alt: "Zhenyu Yin's name card",
          url: '/team/zhenyu-yin',
          personTitle: 'Technology Consultant',
          type: 1
        }
      ]
    }
  },
  computed: {
    investmentPartners() {
      return this.persons.filter((p) => p.type === 0);
    },
    operatingPartners() {
      return this.persons.filter((p) => p.type === 1);
    }
  },
  methods: {
    onleave(e) {
      const menu = document.querySelectorAll('.aside-list__ul')[0];
      menu.className = menu.className.replace(' hover', '');
    },
  },
  components: {
    'aside-list': AsideList
  }
}
</script>

<style lang="scss">
@import '../App.scss';
.page__team,
.page__portfolio-detail {
  padding-top: rem-calc(67);
  background: url('../assets/backgrounds/teamsummarybg.jpg');
  height: 100%;
  background-size: 110%;
  background-repeat: no-repeat;
  @media screen and (max-width: 480px) {
    margin-top: rem-calc(50);
    position: relative;
    .aside-list__aside {
      position: absolute;
      width: 100%;
      max-width: 100%;
      padding: 0;
      left: 0;
      top: 0;
    }
  }
}

.section__team {
  display: block;
  height: rem-calc(580);
  padding-left: rem-calc(50) !important;
  @media screen and (max-width: 480px) {
      max-width: 100% !important;
      flex: 0 0 100% !important;
      display: block;
  }
}

.page__title {
  margin-top: rem-calc(50);
  border-bottom: 2px solid red;
  color: #252525;
  display: inline-block;
  font-size: 1.655469vw;
  font-weight: normal;
  h4 {
    font-family: font033;
    margin-bottom: 0;
  }
}

.profile__list {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

.article__title {
  font-family: font014;
  color: #231815;
  font-size: rem-calc(40);
  margin-top: rem-calc(25);
  margin-bottom: rem-calc(25);
}

.profile__card--horizontal {
  padding: 0 5px 0 0;
  &:last-child {
    padding-right: 0px;
    padding-left: 5px;
  }
}
.profile__card--vertical {
  padding: 0 5px;
}

.profile__name,
.profile__title {
  text-align: center;
  display: block;
}

.profile__name {
  font-family: font858;
  color: #231815;
  font-size: rem-calc(16);
}
.profile__title {
  font-family: font862;
  color: #231815;
  font-size: rem-calc(12);
}
</style>



